import axios from 'axios'
import qs from 'qs'

axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? './' : 'https://www.baidu.com/'

// 
axios.defaults.timeout = 10000;

/**
 * 
 * @param {} config 
 */
axios.interceptors.request.use(function (config) {
    
    // 
    // config.baseURL = '/url/';
    // config.withCredentials = true; // token ,
    // config.timeout = 2500;

    // token
    let token = sessionStorage.getItem('token')
    if (token) {
        config.headers = {
            'token': token,
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    }
    return config;
   }, function (error) {
    // 
    return Promise.reject(error);
});

/**
 * 
 * @param {} response 
 */
axios.interceptors.response.use(function (response){
    // 
   if (response.status === 200) {            
       return Promise.resolve(response);        
   } else {            
       return Promise.reject(response);        
   }
   }, function (error){
   // 
   return Promise.reject(error);
});

/**
 * post
 * @param {} url 
 * url ,axios.defaults.baseURL  
 * ulr='//hn.algolia.com/api/v1/search_by_date?tags=story'  url '//hn.algolia.com/api/v1/search_by_date?tags=story'
 * ulr='/search_by_date?tags=story' url axios.defaults.baseURL + '/search_by_date?tags=story'
 * @param {} params 
 */
export function isPost(url, params) {
    return new Promise((resolve, reject) => {
         axios.post(url, qs.stringify(params))
        .then(res => {
            resolve(res.data);
        })
        .catch(err =>{
            reject(err.data)
        })
    });
}


/**
 * get
 * @param {} url 
 * @param {} params 
 */
export function isGet(url, params){    
    return new Promise((resolve, reject) =>{        
        axios.get(url, {            
            params: params        
        }).then(res => {
            resolve(res.data);
        }).catch(err =>{
            reject(err.data)        
        })    
    });
}